.detail_board {
  /* text-align: center; */
  width: auto;
  border: 1px solid black;
  border-radius: 1%;
  margin: auto;
  display: block;
  font-size: 18px;
  max-width: 600px;
}

.detail_board > table > tr {
  display: block;
  width: 100%;
}
.detail_board > table > tr > td {
  float: left;
  width: 50%;
  margin: 10px 0;
}
.detail_board > table > tr > td:nth-child(2) {
  float: right;
  width: 150px;
  margin-right: 80px;
}

.detail_board > table > tr:nth-child(n + 2) {
  margin-left: 40px;
}

.detail_board > table > .content > td {
  margin: 20px auto 0 auto;
  width: 83%;

  border: 1px solid black;
  padding: 10px;
  min-height: 300px;
  border-radius: 1%;
  /* max-width: 500px; */
}

.title {
  text-align: center !important;
  padding: 30px;
  margin: auto;
  width: 100% !important;
  border-bottom: 1px solid #e2e2e2;
  font-size: 30px !important;
}
